@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cute+Font&display=swap');


:root{
    --color-primary: #6f6af8; 
    --color-white: #fff;
    --color-red: rgb(255, 63, 63);
    --color-gray-100: hsl(220, 20%, 10%, 5%);
    --color-gray-200: hsl(220, 20%, 10%, 10%);
    --color-gray-300: hsl(220, 20%, 10%, 25%);
    --color-gray-500: hsl(220, 20%, 10%, 50%);
    --color-gray-700: #252542;
    --color-gray-900: #0c0c22;

    --color-bg: hwb(240 95% 0%);

    --transition: all 300ms ease;

    --container-width-lg: 84%;
    --container-width-md: 92%;
    --form-width: 50%;

    --radius-1: 0.3rem;
    --radius-2: 0.6rem;
    --radius-3: 1rem;
    --radius-4: 2rem;
}

/*===================================================== GENERAL STYLING ================================================================== */

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;

}

body{
    font-family: "Poppins", "Sans-Serif";
    background: var(--color-bg);
    font-size: 0.9rem;
    line-height: 1.6;
    color: var(--color-gray-700);
}

a{
    color: var(--color-gray-700);
    text-decoration: none;
}

ul{
    list-style: none;
}

h1, h2, h3, h4, h5, h6{
    color: var(--color-gray-900);
    line-height: 1.1;
}

h1{
    font-size: 2.2rem;
}

h2{
    font-size: 1.9rem;
}

h3{
    font-size: 1.3rem;
    line-height: 1.25;
}

h4{
    font-size: 1.1rem;
}

h5{
    line-height: 1.4;
}

img{
    display: block;
    object-fit: cover;
    width: 100%;
}

section{
    margin-top: 6rem;
}

.container{
    width: var(--container-width-lg);
    margin-inline: auto;
}

.btn{
    display: inline-block;
    width: fit-content;
    padding: 0.5rem 1rem;
    border-radius: var(--radius-2);
    background: var(--color-white);
    font-weight: 500;
    cursor: pointer;
    transition: var(--transition);
}

.btn.primary{
    background: var(--color-primary);
    color: var(--color-white);
}

.btn.category{
    background: var(--color-bg);
    color: var(--color-primary);
    font-size: 0.75rem;
    font-weight: 400;
}

.btn.sm{
    font-size: 0.8rem;
    padding: 0.3rem 0.7rem;
    font-weight: 400;
}

.btn.danger{
    background: var(--color-red);
    color: var(--color-white);
}

.btn:hover{
    background: var(--color-gray-900);
    color: var(--color-white);
}

.center{
    text-align: center;
    padding-top: 5rem;
    margin-inline: auto;
    display: block;
    width: 100%;
}

.error-page h2{
    margin-top: 3rem;
}

/*===================================================== NAVBAR STYLING ================================================================== */
nav{
    width: 100vw;
    height: 8rem;
    display: grid;
    place-items: center;
    background: var(255, 255, 255, 0.5);
    position: fixed;
    top:0;
    left: 0;
    z-index: 1;
    background-color:blanchedalmond;
    border-bottom: 4px solid var(--color-bg);
    backdrop-filter: blur(20px);
}

.nav_container {
    display: flex;
    justify-content: space-between; /* Ensure items are spaced evenly */
    align-items: center;
    height: 100%;
}

.nav_logo {
    width: 17rem;
    height: 3rem;
    display: block;
    margin-right: 1rem; /* Add some margin to separate logo from nav_menu */
    transform: translate(-90px,-75px);
}


.nav_menu{
    display: flex;
    align-items: center;
    gap: 3rem;
    transform: translateY(10px);
    font-family: 'Source Code Pro', monospace;
    text-transform: uppercase;
}

.nav_menu a:hover{
    color: coral;
}

.nav_toggle-btn{
    display: none;
}

/*===================================================== FOOTER STYLING ================================================================== */

footer{
    background: var(--color-gray-900);
    margin-top: 555px;
    padding-top: 6rem;
  
}

.footer_categories{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 4rem;
}

.footer_categories a{
    background-color: var(--color-gray-700);
    color: var(--color-white);
    padding: 0.6rem 1.4rem;
    border-radius: var(--radius-2);
    transition: var(--transition);
}

.footer_categories a:hover{
    background: var(--color-white);
    color: var(--color-gray-900);
}

.footer_copyright{
    text-align: center;
    border-top: 2px solid var(--color-gray-700);
    padding: 1rem;
    color: var(--color-white);
    font-family: 300;
}
/*===================================================== HOME SECTION ================================================================== */
/*===================================================== POST PAGE STYLING ================================================================== */

.posts_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
    margin-top: 185px;
}
.posts_title{
    display: flex;
    justify-content: center;
    margin-top: 155px;
    font-family: 'Cute Font', sans-serif;
}
.posts_title h1{
    font-size: 70px;
    color:var(--color-primary);
}
.post{
    background: var(--color-white);
    padding: 1rem;
    border-radius: var(--radius-4);
    padding-bottom: 2rem;
    transition: var(--transition);
    cursor: default;
}

.post:hover{
    box-shadow: 0 1.5rem 2rem rgba(0,0,0,0.1);
}

.post_thumbnail{
    border-radius: var(--radius-3);
    overflow: hidden;
    height: 16rem;
}

.post_content{
    margin-top: 1.5rem;
}

.post_content h3{
    font-weight: 500;
    text-transform: capitalize;
    font-family: 'Oswald', sans-serif;
    margin: 1rem 0 0.6rem;
}

.post_content p {
    font-weight: 300;
    font-family: 'Oswald', sans-serif;
    font-size: larger;
}

.post_footer{
    display: flex;
    justify-content:space-between;
    align-items: flex-end;
    margin-top: 2rem;
}

.post_author{
    display: flex;
    gap: 1rem;
    align-items: flex-start;
}

.post_author-av{
    width: 2.5rem;
    aspect-ratio: 1/1;
    border-radius: var(--radius-2);
    overflow: hidden;
}

/*===================================================== SINGLE POST STYLING ============================================================== */
.post-detail{
    width: var(--form-width);
    background: var(--color-white);
    margin-inline: auto;
    padding: 10rem 0;
    
}

.post-detail_header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.5rem;
}

.post-detail_buttons{
    display: flex;
    gap: 1rem;
    align-items: center;
}

.post-detail_thumbnail{
    margin: 1rem 0;
    height: fit-content;
    max-height: 30rem;
    overflow: hidden;
}

.post-detail p{
    margin-bottom: 0.7rem;
}

/*===================================================== REGISTER AND FORM STYLING ====================================================== */  
.form{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}

input, select, textarea{
    width: 100%;
    padding: 0.6rem 1rem;
    border-radius: var(--radius-1);
    resize: none;
}

.form_error-message{
    background: var(--color-red);
    color: var(--color-white);
    font-size: 0.8rem;
    padding: 0.6rem 1rem;
    display: block;
    margin-bottom: 0.5rem;
}

.register, .login, .create-post{
    width: var(--form-width);
    margin-inline: auto;
    height: calc(100vh - 12rem);
    display: grid;
    place-items: center;
}

.register h2, .login h2, .create-post h2{
    margin-bottom: 2rem;
}

.register small, .login small{
    display: block;
    margin-top: 1.5rem;
}

.register small a, .login small a{
   color: var(--color-primary);
}

/* custom style for react quill */
.ql-editor{
    overflow: scroll;
    background: var(--color-white);
    height: 10rem !important;
}


/*===================================================== AUTHORS PAGE STYLING ============================================================ */

.authors_container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin-top: 300px;
}

.author{
    background: var(--color-white);
    padding: 1rem;
    border-radius: var(--radius-3);
    display: flex;
    gap: 1rem;
    transition: var(--transition);
}

.author:hover{
    box-shadow: 0 1rem 1.5rem rgba(0,0,0,0.07);
    background-color: #6f6af8;
}



.author_avatar{
    width: 5rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    border: 0.3rem solid var(--color-bg);
    overflow: hidden;
}

.author_info p{
    margin-top: 1rem;
}

/*===================================================== USER PROFILE STYLING ============================================================ */

.profile_container{
    display: grid;
    place-items: center;
    text-align: center;
    margin-top: 155px;
}

.profile_details{
    width: 100%;
}

.avatar_wrapper{
    width: 15rem;
    aspect-ratio: 1/1;
    position: relative;
    margin-inline: auto;
    margin-top: 1rem;
}

.profile_avatar{
    height: 100%;
    border-radius: 50%;
    border: 1rem solid var(--color-white);
    overflow: hidden;
}

.avatar_form{
    height: 1rem;
}

.avatar_form input{
    visibility: hidden;
}

.avatar_form label, .profile_avatar-btn{
    background: var(--color-gray-900);
    color: var(--color-white);
    font-size: 1.3rem;
    position: absolute;
    right: 2rem;
    bottom: 1.4rem;
    width: 3rem;
    height: 3rem;
    display: grid;
    place-items: center;
    border-radius: 50%;
}

.profile_avatar-btn{
    cursor: pointer;
    padding: 0;
    background: var(--color-primary);
}

.profile_details h1{
    margin-top: 1.5rem;
}

.form.profile_form{
    margin-top: 3rem;
    margin-inline: auto;
    width: var(--form-width);
}

/*===================================================== USER DASHBOARD STYLING =========================================================== */

.dashboard_container{
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
    margin-top: 175px;
}

.dashboard_post{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    background: var(--color-white);
    padding: 0.5rem 1.2rem;
    border-radius: var(--radius-3);
}

.dashboard_post-info{
    display: flex;
    gap: 2rem;
    width: 100%;
    align-items: center;
}

.dashboard_post-thumbnail{
    width: 4rem;
    border-radius: var(--radius-1);
    overflow: hidden;
}

.dashboard_post-actions{
    display: flex;
    gap: 1rem;
}

/*=====================================================LOADER STYLES ================================================================== */
.loader{
    position: fixed;
    top:0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
    background-color: antiquewhite;
}

.loader_image{
    width: 65rem;
    height: 75rem;
    aspect-ratio: 1/1;
}

/*=====================================================SLIDER STYLES ================================================================== */

.slider-container {
    position: relative;
    width: 100%;
    max-width: 1000px; 
    margin: 0 auto;
  }
  
  .slide {
    position: relative;
    width: 100%;
  }
  
  .image {
    width: 100%;
    height: auto;
    border-radius: 0.5rem;
  }
  .headline{
    position: absolute;
    bottom: 80px;
  }
  .text-headline{
    position: relative;
    font-size: 35px;
    font-family: 'Oswald', sans-serif;
    text-transform: capitalize;
    color: white;
    margin-left: 2rem;
  }
  .subline{
    position: absolute;
    bottom: 45px;
  }
  .text-subline{
    position: relative;
    font-size: 14px;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    color: white;
    margin-left: 2rem;
  }
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 25%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .text {
    color: white;
    font-size: 14px;
    padding: 10px;
    margin: 0;
  }
    
  .prev-button, .next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px;
    font-size: 20px;
    transition: background-color 0.3s;
    z-index: 1; /* Ensure buttons are above the image */
  }
  
  .prev-button:hover, .next-button:hover {
    background-color: #6f6af8;
  }
  
  .prev-button {
    left: 0;
  }
  
  .next-button {
    right: 0;
  }
  
  


/*===================================================== MEDIA QUERIES ================================================================== */

@media screen and (max-width: 1543px){
    .nav_logo{
        display: none;
    }
    
}
@media screen and (max-width: 1225px) {
    h1{
        font-size: 2rem;
    }
    h2{
        font-size: 1.6rem;
    }
    h3{
        font-size: 1.35rem;
    }
    .container{
        width: var(--container-width-md);
    }

    /* NAV */

    .nav_container{
        position: relative;
    }

    .nav_logo{
        display: none;
    }

    .nav_menu{
        position: absolute;
        top: 5rem;
        right: 0;
        flex-direction: column;
        gap: 0;
    }

    .nav_menu li {
        width: 100%;
        box-shadow: -2rem 4rem 4rem rgba(0,0,0,0.27);
        animation: dropDown 500ms ease forwards;
        opacity: 0;
        transform: rotateX(90deg);
        transform-origin: top;
    }

    @keyframes dropDown{
        to{
            opacity: 1;
            transform: rotateX(0);
        }
    }

    .nav_menu li:nth-child(2){
        animation-delay: 300ms;
    }
    .nav_menu li:nth-child(3){
        animation-delay: 600ms;
    }
    .nav_menu li:nth-child(4){
        animation-delay: 900ms;
    }
    .nav_menu li:nth-child(5){
        animation-delay: 1200ms;
    }
    .nav_menu li:nth-child(6){
        animation-delay: 1500ms;
    }
    .nav_menu li:nth-child(7){
        animation-delay: 1800ms;
    }
    .nav_menu li:nth-child(8){
        animation-delay: 2100ms;
    }
    .nav_menu li:nth-child(9){
        animation-delay: 2400ms;
    }
    .nav_menu li:nth-child(10){
        animation-delay: 2700ms;
    }

    .nav_menu li a {
        display: block;
        background: var(--color-white);
        padding: 0.85rem 2.5rem;
    }

    .nav_toggle-btn{
        display: inline-block;
        background: transparent;
        font-size: 1.5rem;
        cursor: pointer;
    }

    .posts_container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .footer_categories{
        flex-direction: column;
    }

    .post-detail{
        width: 100%;
        background: transparent;
        padding: 0;
    }

    .post-detail h1{
        margin-top: 1rem;
    }
    

    .register, .login, .create-post{
        width: 100%;
    }

    .form.profile_form{
        margin-top: 2.5rem;
        width: 100%;
    }

    .authors_container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .dashboard_post{
        flex-direction: column;
    }
    
    .dashboard_post-info{
        gap: 1.5rem;
    }

    .dashboard_post-thumbnail{
        width: 5rem;
    }
}

@media screen and (max-width: 755px){
    .slider-container{
        display: none;
    }
}